/* Toggle functionality */
#toggle {
  position: absolute;
  left: -100%;
  top: -100%;
}

#toggle:focus ~ .toggle-container .button-toggle {
  box-shadow: 0 0 0 8px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1);
}

#toggle:checked ~ .toggle-container .button-toggle {
  box-shadow: 0 0 0 550px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1);
}
#toggle:checked ~ .toggle-container .button-toggle:hover {
  box-shadow: 0 0 0 550px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1), 0 0 0 8px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1);
}
#toggle:checked ~ .toggle-container .button-toggle:before {
  transform: translateY(-50%) rotate(45deg) scale(1);
}
#toggle:checked ~ .toggle-container .button-toggle:after {
  transform: translateY(-50%) rotate(-45deg) scale(1);
}
#toggle:checked:focus ~ .toggle-container .button-toggle {
  box-shadow: 0 0 0 550px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1), 0 0 0 8px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1);
}
#toggle:checked ~ .nav {
  margin-bottom: 100px;
  pointer-events: auto;
  margin: 14px 25px 0 60px;
}
#toggle:checked ~ .nav a {
  color: #fff;
  letter-spacing: 0;
  height: 40px;
  line-height: 40px;
  margin-top: 0;
  opacity: 1;
  transform: scaleY(1);
  transition: 0.5s, opacity 0.1s;
}
#toggle:checked ~ .nav a:nth-child(1) {
  transition-delay: 0.15s;
}
#toggle:checked ~ .nav a:nth-child(1):before {
  transition-delay: 0.15s;
}
#toggle:checked ~ .nav a:nth-child(2) {
  transition-delay: 0.1s;
}
#toggle:checked ~ .nav a:nth-child(2):before {
  transition-delay: 0.1s;
}
#toggle:checked ~ .nav a:nth-child(3) {
  transition-delay: 0.05s;
}
#toggle:checked ~ .nav a:nth-child(3):before {
  transition-delay: 0.05s;
}
#toggle:checked ~ .nav a:nth-child(4) {
  transition-delay: 0s;
}
#toggle:checked ~ .nav a:nth-child(4):before {
  transition-delay: 0s;
}
#toggle:checked ~ .nav a:before {
  opacity: 0;
}

/* Toggle button */
.button-toggle {
  position: absolute;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 25px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  border-radius: 100%;
  transition: 0.6s;
}
.button-toggle:hover {
  box-shadow: 0 0 0 8px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1);
}
.button-toggle:before, .button-toggle:after {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fff;
  border-radius: 5px;
  transition: 0.5s;
}
.button-toggle:before {
  transform: translateY(-50%) rotate(45deg) scale(0);
}
.button-toggle:after {
  transform: translateY(-50%) rotate(-45deg) scale(0);
}

/* Menu */
.nav {
  display: grid;
  margin: 25px 25px 20px;
  pointer-events: none;
  transition: 0.5s;
}

.nav a {
  position: relative;
  display: inline-block;
  float: left;
  clear: both;
  color: transparent;
  font-size: 14px;
  letter-spacing: -6.2px;
  height: 7px;
  line-height: 7px;
  text-transform: uppercase;
  white-space: nowrap;
  transform: scaleY(0.2);
  transition: 0.5s, opacity 1s;
}
.nav a:nth-child(1) {
  transition-delay: 0s;
}
.nav a:nth-child(1):before {
  transition-delay: 0s;
}
.nav a:nth-child(2) {
  transition-delay: 0.05s;
}
.nav a:nth-child(2):before {
  transition-delay: 0.05s;
}
.nav a:nth-child(3) {
  transition-delay: 0.1s;
}
.nav a:nth-child(3):before {
  transition-delay: 0.1s;
}
.nav a:nth-child(4) {
  transition-delay: 0.15s;
}
.nav a:nth-child(4):before {
  transition-delay: 0.15s;
}
.nav a:nth-child(1) {
  letter-spacing: -8px;
}
.nav a:nth-child(2) {
  letter-spacing: -7px;
}
.nav a:nth-child(n+4) {
  letter-spacing: -8px;
  margin-top: -7px;
  opacity: 0;
}
.nav a:before {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  width: 100%;
  height: 3px;
  width: 20px;
  background-color: #fff;
  transform: translateY(-50%) scaleY(5);
  transition: 0.5s;
}

